.banner {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 50px;
}

.banner-text {
  margin: 0 50px;
  max-width: 40%;
}

.headshot {
  width: 100%;
  height: auto;
}

.home-subsection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 0 50px 0;
}

/***************** Mobile Responsive Layouts *****************/

@media screen and (max-width: 900px) {
  .banner {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 0;
  }
  .banner-text {
    max-width: 100%;
    text-align: center;
    margin: 20px;
  }
}

@media screen and (max-width: 600px) {
  .home-subsection {
    text-align: center;
  }
}



